body {
  font-family: 'Roboto Condensed', sans-serif;
  text-align: center; }

.jumbotron h1 {
  font-size: 40px !important; }

.jumbotron h2 {
  font-size: 35px !important; }

h2 {
  font-weight: bold; }

h3 {
  font-weight: bold;
  text-transform: uppercase;
  margin: 20px 0; }

.why, .info, .main, .delivery, .contacts {
  border-bottom: 1.5px solid #A0A197;
  padding: 20px 50px; }

.text {
  display: -ms-flexbox;
  display: flex; }

.info h4 {
  font-family: cursive;
  margin-top: 30px; }

.info img {
  height: 200px;
  width: 30%; }

.info > div {
  margin-top: 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin-bottom: 10px; }
  @media (max-width: 800px) {
    .info > div {
      -ms-flex-direction: column;
          flex-direction: column; }
      .info > div img {
        width: auto;
        max-width: 400px; }
      .info > div p {
        width: 100%;
        margin-left: 0px;
        margin-top: 20px; } }

.text p {
  width: 70%;
  margin-left: 20px;
  text-align: justify;
  text-indent: 30px;
  font-size: 20px; }

.contacts ul {
  list-style-type: none;
  margin-left: -50px; }

::-moz-selection {
  background-color: #72E48A;
  color: #fff; }

::selection {
  background-color: #72E48A;
  color: #fff; }

/* The animation code */
@-webkit-keyframes example {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }
@keyframes example {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

/* The element to apply the animation to */
.animatio {
  -webkit-animation-name: example;
          animation-name: example;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s; }

body:not(.menu-on-top).desktop-detected {
  min-height: 0 !important; }

body::-webkit-scrollbar {
  width: 0.7em; }

body::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3); }

body::-webkit-scrollbar-thumb {
  border-radius: 3px;
  background-color: darkgrey;
  outline: 1px solid slategrey; }



